<template>
  <h1>Calendario Dailys</h1>
  <div>
    <DatePicker v-model="date" mode="date" />
    <div class="divTable" style="padding-top: 20px">
      <div class="divTableBody">
        <div class="divTableRow">
          <div class="divTableCell title">Semana del año:</div>
          <div class="divTableCell item" style="padding-left: 25px;">{{ this.moment(date).isoWeek() }}</div>
        </div>
        <div class="divTableRow">
          <div class="divTableCell title">Día del año:</div>
          <div class="divTableCell item" style="padding-left: 25px;">{{ this.moment(date).dayOfYear() }}</div>
        </div>
        <div class="divTableRow">
          <div class="divTableCell title">Responsable daily:</div>
          <div class="divTableCell item">
            <div class="divTableCell"><img :src="`${team[turn()].image}`" :alt="`${team[turn()].nombre}`" style="width: 60px;" /></div>
            <div class="divTableCell">{{ team[turn()].nombre }}</div>
          </div>
        </div>
        <div class="divTableRow">
          <div class="divTableCell title">Backup responsable daily:</div>
          <div class="divTableCell">
            <div class="divTableCell"><img :src="`${team[turnBackUp()].image}`" :alt="`${team[turnBackUp()].nombre}`" style="width: 60px;" /></div>
            <div class="divTableCell">{{ team[turnBackUp()].nombre }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { DatePicker } from "v-calendar";
import { ref } from "vue";
import momentjs from "moment";
import Katherine_Soto_Aguilar from "@/assets/Katherine_Soto_Aguilar.png";
import Trinidad_Meneses from "@/assets/Trinidad_Meneses.png";
import Carlos_Morales from "@/assets/Carlos_Morales.png";
import Ian_Wells from "@/assets/Ian_Wells.png";
import Juan_Lopez from "@/assets/Juan_Lopez.png";
import Juan_Mora from "@/assets/Juan_Mora.png";
import Keynes_Nogues from "@/assets/Keynes_Nogues.png";
import Oscar_Morales from "@/assets/Oscar_Morales.png";
import Sebastian_Salinas from "@/assets/Sebastian_Salinas.png";
import Victor_Yunis from "@/assets/Victor_Yunis.png";
import Armando_Rojas from "@/assets/Armando_Rojas.png";
import Abdo_Anais from "@/assets/Abdo_Anais.png";
import Andres_Arancibia from "@/assets/Andres_Arancibia.png";

export default {
  components: {
    DatePicker,
  },
  setup() {
    const team = [
      {
        nombre: "Juan Pablo Mora",
        image: Juan_Mora,
        turn: 0,
      },
      {
        nombre: "Trinidad Meneses",
        image: Trinidad_Meneses,
        turn: 1,
      },
      {
        nombre: "Andrés Arancibia",
        image: Andres_Arancibia,
        turn: 2,
      },
      {
        nombre: "Abdo Anais",
        image: Abdo_Anais,
        turn: 3,
      },
      {
        nombre: "Keynes Nogues",
        image: Keynes_Nogues,
        turn: 4,
      },
      {
        nombre: "Carlos Morales",
        image: Carlos_Morales,
        turn: 5,
      },
      {
        nombre: "Juan Carlos López",
        image: Juan_Lopez,
        turn: 6,
      },
      {
        nombre: "Armando Rojas",
        image: Armando_Rojas,
        turn: 7,
      },
      {
        nombre: "Ian Wells",
        image: Ian_Wells,
        turn: 8,
      },
      {
        nombre: "Oscar Morales",
        image: Oscar_Morales,
        turn: 9,
      },
      {
        nombre: "Sebastian Salinas",
        image: Sebastian_Salinas,
        turn: 10,
      },
      {
        nombre: "Victor Yunis",
        image: Victor_Yunis,
        turn: 11,
      },
      {
        nombre: "Katerine Soto-Aguilar",
        image: Katherine_Soto_Aguilar,
        turn: 12,
      },      
    ];
    const moment = momentjs;
    let date = ref(new Date());
    let isDayOfWeek = () =>
      date.value != null &&
      moment(date.value).weekday() >= 1 &&
      moment(date.value).weekday() <= 5;
    let turn = () => moment(date.value).dayOfYear() % team.length;
    let turnBackUp = () =>
      turn() + 2 > team.length - 1 ? (turn() + 2) % team.length : turn() + 2;

return {
      date,
      team,
      turn,
      turnBackUp,
      isDayOfWeek,
      moment,
    };
  },
};
</script>

<style>
/* DivTable.com */
.divTable{
	display: table;
	width: 100%;
}
.divTableRow {
	display: table-row;
}
.divTableHeading {
	background-color: #EEE;
	display: table-header-group;
}
.divTableCell, .divTableHead {
	/*border: 1px solid #999999;*/
	display: table-cell;
	padding: 3px 10px;
  vertical-align: middle;
}
.divTableHeading {
	background-color: #EEE;
	display: table-header-group;
	font-weight: bold;
}
.divTableFoot {
	background-color: #EEE;
	display: table-footer-group;
	font-weight: bold;
}
.divTableBody {
	display: table-row-group;
}
.title {
  text-align: right;
  font-weight: bold;
  vertical-align: middle;
}
.item {
  text-align: left;
}
</style>
